import axios from 'axios';
import React, { useState } from 'react';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import logo from '../images/pulsar.svg';
import ghap from '../images/ghap.PNG';
import galaxy from '../images/galaxy.jpeg';
import '../design.css';
import Toast from './Toast';

function GhapLogin() {
  var html = 'https://pulsarapp-server.herokuapp.com';

  const changeState = useDispatch();
  const viewLogin = useSelector((state) => state.login);

  var usuario = null;
  var senha = null;

  // carregamento da janela.
  useEffect(() => {
    if (viewLogin === 1) {
      // limpando os campos de login e senha.
      setwelcome(0);
      document.getElementById('inputUsuario').value = '';
      document.getElementById('inputSenha').value = '';

    }
  }, [viewLogin]);

  // definindo a lista de registros da TABELA USUÁRIOS.
  const [listusuarios, setlistusuarios] = useState([]);
  /* definindo o valor o tipo de usuário (recepcionista, secretária, gestor,
    plantonista ou especialista), conforme o valor lançado no "inputUsuario". */
  const setLogin = () => {
    usuario = document.getElementById('inputUsuario').value;
    // ROTA: SELECT * FROM usuarios WHERE usuario = usuario.
    axios
      .get(html + "/login/'" + usuario.toUpperCase() + "'")
      .then((response) => {
        setlistusuarios(response.data);
        var x = [0, 1];
        x = response.data;
        // mapeando o tipo de usuário.
        const arraytipo = x.map((item) => item.tipo);
        const arraynome = x.map((item) => item.firstname);
        const logintipo = arraytipo[0];
        if (x.length > 0 && logintipo === 1) { // plantonista.
          settypeuser('OLÁ, DR(A). ' + arraynome + '.')
          setwelcome(1);
        } else if (x.length > 0 && logintipo === 2) { // gestor.
          settypeuser('OLÁ, CHEFE!')
          setwelcome(1);
        } else if (x.length > 0 && logintipo === 3) { // secretária.
          settypeuser('OLÁ, ' + arraynome + '.')
          setwelcome(1);
        } else if (x.length > 0 && logintipo === 4) { // técnico.
          settypeuser('OLÁ, ' + arraynome + '.')
          setwelcome(1);
        } else if (x.length > 0 && logintipo === 5) { // enfermeiro.
          settypeuser('OLÁ, ' + arraynome + '.')
          setwelcome(1);
        } else if (x.length > 0 && logintipo === 6) { // enfermeiro.
          settypeuser('OLÁ, ' + arraynome + '.')
          setwelcome(1);
        }
      });
  };

  // função que retorna o valor de login ao digitarmos no campo login e busca os hospitais.
  const [welcome, setwelcome] = useState(0);
  const [typeuser, settypeuser] = useState(0);
  function Welcome() {
    if (welcome === 1) {
      return (
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <div
            className="title2 fade-in"
            style={{
              fontSize: 14,
              height: 50,
              padding: 10,
              marginTop: 10,
              marginBottom: 5,
            }}
          >
            {typeuser}
          </div>
          <div>
            <button
              onClick={() => checkLogin()}
              className="blue-button"
              style={{ width: 100 }}
            >
              ENTRAR
            </button>
          </div>
        </div>
      );
    } else {
      return (
        <div
          style={{
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <div
            className="title2 fade-in"
            style={{
              fontSize: 14,
              height: 50,
              padding: 10,
              marginTop: 10,
              marginBottom: 5,
              color: 'transparent',
            }}
          >
            {'xxx'}
          </div>
          <div>
            <button
              onClick={() => checkLogin()}
              className="blue-button"
              style={{ width: 100 }}
            >
              OK
            </button>
          </div>
        </div>
      );
    }
  }

  // função que busca um registro de login válido no servidor.
  const checkLogin = () => {
    usuario = document.getElementById('inputUsuario').value;
    senha = document.getElementById('inputSenha').value;
    var obj = {
      usuario: usuario.toUpperCase(),
      senha: senha.toUpperCase(),
    };
    // ROTA: SELECT * FROM usuarios WHERE usuario = usuario AND senha = senha.
    axios
      .post(html + "/login/'" + usuario + "'/'" + senha.toUpperCase() + "'", obj)
      .then((response) => {
        var x = [0, 1];
        x = response.data;
        // mapeando id, nome e tipo de usuário.
        const arrayid = x.map((item) => item.id);
        const loginid = arrayid[0];
        const arraynome = x.map((item) => item.firstname);
        const loginnome = arraynome[0];
        const arrayfuncao = x.map((item) => item.funcao);
        const loginfuncao = arrayfuncao[0];
        const arraytipo = x.map((item) => item.tipo);
        const logintipo = arraytipo[0];
        setTimeout(function () {
          // usuário secretária.
          if (x.length > 0 && logintipo === 3) {
            changeState({
              type: 'SECRETARIA_ON',
              // payloads.
              payloadIdusuario: loginid,
              payloadNomeusuario: loginnome,
              payloadTipo: logintipo,
            });
            // usuário plantonista ou gestor.
          } else if (x.length > 0 && logintipo !== 3) {
            changeState({
              type: 'HOSPITAL_ON',
              // payloads.
              payloadIdusuario: loginid,
              payloadNomeusuario: loginnome,
              payloadFuncao: loginfuncao,
              payloadTipo: logintipo,
            });
          } else {
            toast(1, '#ec7063', 'ERRO AO EFETUAR LOGIN.', 3000);
          }
        }, 1000);
      });
  };

  // função para construção dos toasts.
  const [valor, setvalor] = useState(0);
  const [cor, setcor] = useState('transparent');
  const [mensagem, setmensagem] = useState('');
  const [tempo, settempo] = useState(2000);
  const toast = (value, color, message, time) => {
    setvalor(value);
    setcor(color);
    setmensagem(message);
    settempo(time);
    setTimeout(() => {
      setvalor(0);
    }, time + 1000);
  }

  // renderização do componente.
  if (viewLogin === 1) {
    return (
      <div className="main fade-in">
        <Toast valor={valor} cor={cor} mensagem={mensagem} tempo={tempo}></Toast>
        <img className="slide" alt="" src={galaxy}
          style={{
            position: 'absolute', top: 0, bottom: 0, left: -50, right: -50,
            width: window.innerWidth > 800 ? '100%' : '120%', height: '100%',
          }}></img>
        <div className="pulsar-in"
          style={{
            position: 'absolute', top: 0, bottom: 0, left: 0, width: window.innerWidth > 800 ? '50%' : '100%',
            backgroundColor: window.innerWidth > 800 ? '#ffffff' : '#003878',
            display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'
          }}>
          <img alt="" src={logo} style={{ display: window.innerWidth > 800 ? 'flex' : 'none', height: '25%' }}></img>
          <text className="title2" style={{ margin: 10, marginTop: window.innerWidth > 800 ? 15 : -100, marginBottom: window.innerWidth > 800 ? 0 : 50, fontSize: 26, color: window.innerWidth < 800 ? '#ffffff' : '' }}>GPULSE</text>
          <text className="title2" style={{ position: 'absolute', bottom: 0, margin: 10, fontSize: 10, color: window.innerWidth < 800 ? '#ffffff' : '' }}>Powered By GHAP Tecnologia</text>
        </div>
        <div style={{
          position: 'absolute', top: 0, bottom: 0, right: 0, width: window.innerWidth > 800 ? '50%' : '100%',
          backgroundColor: window.innerWidth > 800 ? '#003878' : 'transparent',
          borderRadius: window.innerWidth > 800 ? 0 : 5,
          display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center'
        }}>
          <img alt="" src={window.innerWidth > 800 ? ghap : logo} style={{ height: '20%', marginTop: window.innerWidth > 800 ? 0 : 0, marginBottom: window.innerWidth > 800 ? 0 : 60 }}></img>
          <div
            style={{
              marginTop: 20,
              display: 'flex',
              flexDirection: 'column',
              justifyContent: 'center',
              alignItems: 'center',
              borderRadius: 5, backgroundColor: '#ffffff', padding: 20
            }}
          >
            <div>
              <input
                autoComplete="off"
                placeholder="LOGIN"
                className="input"
                type="text"
                id="inputUsuario"
                onChange={() => setLogin()}
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'LOGIN')}
                style={{
                  marginTop: 10,
                  marginBottom: 10,
                  width: 200,
                  height: 50,
                }}
              ></input>
            </div>
            <div>
              <input
                autoComplete="off"
                placeholder="SENHA"
                className="input"
                type="password"
                id="inputSenha"
                onFocus={(e) => (e.target.placeholder = '')}
                onBlur={(e) => (e.target.placeholder = 'SENHA')}
                style={{
                  marginTop: 0,
                  marginBottom: 0,
                  width: 200,
                  height: 50,
                }}
              ></input>
            </div>
            <Welcome></Welcome>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
}
export default GhapLogin;
